import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import useEmblaCarousel from "embla-carousel-react";
import Autoplay from "embla-carousel-autoplay";

const StackSection = () => {
  const [emblaRef, emblaApi] = useEmblaCarousel(
    { loop: true, dragFree: true, duration: 10000, align: 0 },
    [
      Autoplay({
        delay: 0,
        stopOnInteraction: false,
        stopOnMouseEnter: true,
      }),
    ]
  );

  const techData = useStaticQuery(query);
  const techArr = techData?.markdownRemark?.frontmatter?.stack as string[];

  return (
    <section className={`h-28`} id="clients">
      <div className="h-full overflow-hidden" ref={emblaRef}>
        <div
          className="h-full flex items-center"
          onMouseEnter={() => {
            emblaApi?.internalEngine().animation.stop();
          }}
          onMouseLeave={() => {
            emblaApi?.internalEngine().animation.start();
          }}
        >
          {techArr.map((tech) => (
            <div
              key={tech}
              className="h-full shrink-0 min-w-0 px-24 2xl:px-36 -mx-2 cursor-pointer flex items-center content-center border-y-8 border-dashed"
            >
              <p className="text-4xl font-extrabold whitespace-nowrap">
                {tech}
              </p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default StackSection;

export const query = graphql`
  query {
    markdownRemark(fields: { sourceName: { eq: "stack" } }) {
      frontmatter {
        stack
      }
    }
  }
`;
