import React from "react";

import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { IReview } from "../types/review.type";

const Review = ({
  data: { company, person, position, location, text, image },
}: {
  data: IReview;
}) => {
  const avatar = getImage(image);

  return (
    <div
      className="flex flex-col md:flex-row flex-nowrap p-6 text-main-100 my-2"
      itemProp="review"
      itemScope
      itemType="https://schema.org/Review"
    >
      <div
        className="basis-1/3 p-2 flex flex-col items-center justify-center mb-10 md:mb-0"
        itemProp="author"
        itemScope
        itemType="https://schema.org/Person"
      >
        <div
          className={`rounded-full h-28 w-28 mx-auto mb-8 overflow-hidden flex justify-center items-center`}
        >
          {avatar ? (
            <GatsbyImage
              image={avatar}
              alt={company}
              className="h-full object-cover"
              itemProp="image"
            />
          ) : null}
        </div>
        <h5
          className="text-xl font-extrabold whitespace-nowrap"
          itemProp="familyName"
        >
          {person}
        </h5>
        <h5 className="text-xl font-extrabold whitespace-nowrap">
          <span itemProp="jobTitle">{position}</span>,{" "}
          <span itemProp="worksFor">{company}</span>
        </h5>
        <h5
          className="text-xl font-extrabold whitespace-nowrap"
          itemProp="workLocation"
        >
          {location}
        </h5>
      </div>
      <div
        className="shrink-0 basis-2/3 p-2 font-normal text-2xl text-justify leading-[1.375rem]"
        itemProp="reviewBody"
      >
        {text}
      </div>
    </div>
  );
};

export default Review;
