import React from "react";

import { graphql, useStaticQuery } from "gatsby";

import { IPostPreviewQueryResult } from "../types/post.type";

import PostPreview from "./postPreview";
import TemplateSection from "./templateSection";

const PostSection = () => {
  const postData = useStaticQuery(query);

  const post = (
    postData?.allMarkdownRemark?.nodes?.length
      ? postData.allMarkdownRemark.nodes[0]
      : null
  ) as IPostPreviewQueryResult | null;

  return (
    <>
      {post ? (
        <TemplateSection
          title="new in our blog"
          linkTitle="all posts"
          section="blog"
          className="bg-main-100 text-secondary-100"
        >
          <PostPreview post={post} fontColor="secondary" />
        </TemplateSection>
      ) : null}
    </>
  );
};

export default PostSection;

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "posts" } } }
      sort: { frontmatter: { date: DESC } }
      limit: 1
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "MMM DD, YYYY")
          title
          text
        }
        wordCount {
          words
        }
      }
    }
  }
`;
