import React from "react";

import Border from "../assets/common/border.svg";

import ContentContainer from "./contentContainer";

const Banner = ({
  heightClass = "h-160",
  title,
  text,
  className,
  borderBottom,
  borderTop,
}: {
  heightClass?: string;
  title: string;
  text: string;
  className?: string;
  borderBottom?: boolean;
  borderTop?: boolean;
}) => {
  return (
    <div
      className={`w-full bg-main-100 relative overflow-hidden px-6 ${heightClass} ${className}`}
    >
      {borderTop ? (
        <Border className="absolute top-0 left-0 right-0 w-full" />
      ) : null}
      <ContentContainer
        className={`h-full flex-col items-left justify-center overflow-hidden text-secondary-100`}
      >
        <h2 className="font-['Rubik_Glitch'] text-5xl sm:text-6xl mb-8 w-full text-center md:text-start md:w-1/2">
          {title.toUpperCase()}
        </h2>
        <p className="w-full text-3xl sm:text-4xl font-light text-center md:text-start md:w-1/2">
          {text.toUpperCase()}
        </p>
      </ContentContainer>
      {borderBottom ? (
        <Border className="absolute bottom-0 left-0 right-0 w-full" />
      ) : null}
    </div>
  );
};

export default Banner;
