import React from "react";

import { Link } from "gatsby";

import Quote from "../assets/common/chat-right-quote.svg";
import Clock from "../assets/common/clock.svg";

import { IPostPreviewQueryResult } from "../types/post.type";
import { LocalBusinessMetadata } from "./metadata";

const PostPreview = ({
  post,
  className,
  fontColor,
}: {
  post: IPostPreviewQueryResult;
  className?: string;
  fontColor: "main" | "secondary";
}) => {
  const { date, title, text } = post.frontmatter;

  const minutes = Math.floor(post.wordCount.words / 200);
  const readingTime = minutes ? minutes : 1;

  const fontColorClass100 =
    fontColor === "main" ? "text-main-100" : "text-secondary-100";
  const fontColorClass10 =
    fontColor === "main" ? "text-main-100" : "text-secondary-10";
  const fontColorHover =
    fontColor === "main" ? "hover:text-main-100" : "hover:text-secondary-100";

  return (
    <Link
      to={`/blog/${post.id}`}
      className={`flex flex-col sm:flex-row w-full ${fontColorClass10} ${fontColorHover} ${className}`}
      itemScope
      itemType="https://schema.org/Article"
    >
      <div
        className="hidden md:flex md:basis-1/4 shrink-0 justify-center p-6"
        itemProp="image"
      >
        <Quote className={fontColor === "secondary" ? "" : "brightness-0"} />
      </div>
      <div className="flex flex-col basis-1/2 md:basis-1/3 shrink-0 mb-12 sm:mb-0">
        <p className={`text-3xl font-light ${fontColorClass10} pb-5`}>{date}</p>
        <h3
          className={`text-4xl font-bold ${fontColorClass100}`}
          itemProp="headline"
        >
          {title}
        </h3>
      </div>
      <div className="flex flex-col basis-1/2 md:basis-1/3 shrink-0">
        <p
          className={`font-normal text-2xl leading-[1.375rem] ${fontColorClass10}`}
          itemProp="abstract"
        >
          {text.split(" ").slice(0, 60).join(" ")}...
        </p>
        <div className="flex justify-between items-center pt-6">
          <p className={`text-2xl font-bold`}>Read more</p>
          <p>
            <Clock className={"inline"} />

            <span className="inline text-xl font-light italic pl-4">
              Reading time: {readingTime}m.
            </span>
          </p>
        </div>
      </div>
      <LocalBusinessMetadata itemProp="copyrightHolder" />
    </Link>
  );
};

export default PostPreview;
